.navbar{
    border-width: 0px 0px 1px 0px;
    border-style: solid;
    border-color: rgba(222, 215, 215, 0.703);
    border-radius: 10px;
}
.page-Title{
    color: aliceblue;
    font-size: 1.5rem;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-width: 0.1px 0.1px 2px 0.1px;
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.725);
    border-radius: 5px;
    padding: 5px 20px;
    margin-right: 20px;
    text-decoration: none;
    box-shadow: 3px 2px 5px rgba(227, 226, 225, 0.63);
}
#accountBtn:active{
    background-color: rgba(125, 124, 124, 0.743);
} 

#homeMenu,#aboutMenu{
    font-size: 1.15rem;
    font-weight: 400;
    margin: 0px 2px;
    padding: 5px;
    
}
#welcomeMsg{
    /* welcome */
    text-align: center;
    margin: 25px 0px;
}
.container textarea{
    border-radius: 10px;
    box-shadow: 2px 2px 5px 5px rgba(128, 128, 128, 0.564);
    margin-bottom: 20x;
    outline: none;
}
.btnBox{
    display: flex;
    justify-content: space-evenly;
    width: 90%;
    margin: auto;
    padding: 10px;
}
.btnBox button{
    font-size: 1rem;
    padding: 10px;
    border-radius: 20px;
}
::selection{
    color: rgb(245, 240, 240);
    background-color: #1d1d1f;
}

.footer{
    height: 12vh;
    margin-top:110px;
    padding: 0px 0px 5px 0px;
    border-width: 1px 0px 0px 0px;
    border-style: solid;
    border-color: rgba(222, 215, 215, 0.703);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.footer span{
    margin-top: 2px;
    margin-bottom: 2px;
}


@media only screen and (max-width:1200px) {
    .btnBox{
        flex-direction: column;
    }
    .btnBox button{
        width: 80vw;
    }
    i{
        color: aliceblue;
    }
    #themeBtn{
        display: block;
    }
    #footer{
        position: relative;
        width: 100%;
        bottom: 0px;
        border-radius: 30px;
        
    }
}