/*login box start*/
.modal-sm{
    max-width: 400px;
}

#login-header{
    display: flex;
    justify-content: center;
}
#close-sign{
    position: absolute;
    right: 20px;
}
#login-body{
    justify-content: space-evenly;
    display: flex;
    flex-direction: column;
    margin: 0px;
    padding: 20px;
}
#login-body input{
    border-width: 0px 0px 1px 0px;
    padding:10px  5px;
    font-size: 1.05rem;
    outline:none;
}
#footerLogin{
    display: flex;
    justify-content: center;
    /* margin: 12px 0px; */
}
#footerLogin a{
    padding: 2px 10px;
    text-decoration: none;
}
#footerLogin button{
    margin: 12px 0px;
    width: 75vw;
}
/*login box end*/